import Vue from 'vue';
import Component from 'vue-class-component';

const map: { [key: string]: string } = {
  'tag name duplicated': ''
};

@Component
export class TagHelper extends Vue {
  createTag() {
    this.$prompt('', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      showClose: false,
      customClass: 'messageBox'
    }).then(res => {
      // @ts-ignore
      const name = res.value
      if (name === '') {
        return;
      }
      if (name === null) {
        return;
      }
      this.$store.commit('createTag', name);
      if (this.$store.state.createTagError) {
        this.$message.warning(map[this.$store.state.createTagError.message] || '');
      }
    }).catch(err => {
      console.error(err)
    })
  }
}

export default TagHelper;